/* Base Styles */
:root {
  --primary-color: #6c63ff;
  --secondary-color: #4d44db;
  --success-color: #28a745;
  --warning-color: #ffc107;
  --danger-color: #dc3545;
  --light-color: #f8f9fa;
  --dark-color: #343a40;
  --gray-color: #6c757d;
  --light-gray: #e9ecef;
  --white: #ffffff;
  --shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  --border-radius: 8px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', sans-serif;
  line-height: 1.6;
  color: #333;
  background-color: #f5f7fa;
}

/* Main Container */
.home-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
  
}

/* Header Styles */
.app-header {
  text-align: center;
  margin-bottom: 2rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid var(--light-gray);
}

.app-title {
  font-size: 2.5rem;
  color: var(--primary-color);
  margin-bottom: 0.5rem;
  font-weight: 700;
}

.app-subtitle {
  font-size: 1rem;
  color: var(--gray-color);
  font-weight: 300;
}

/* Notification Settings */
.notification-settings {
  background-color: var(--white);
  border-radius: var(--border-radius);
  padding: 1.5rem;
  margin-bottom: 2rem;
  box-shadow: var(--shadow);
}

.settings-title {
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: var(--dark-color);
}

.settings-options {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.setting-option {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
}

.setting-option input {
  accent-color: var(--primary-color);
  width: 1.1rem;
  height: 1.1rem;
}

.setting-label {
  font-size: 0.95rem;
  color: var(--dark-color);
}

/* Task Form Styles */
.add-task-form {
  background-color: var(--white);
  border-radius: var(--border-radius);
  padding: 1.5rem;
  margin-bottom: 2rem;
  box-shadow: var(--shadow);
}

.form-group {
  margin-bottom: 1rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
  color: var(--gray-color);
  font-weight: 500;
}

.form-row {
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
}

.form-row .form-group {
  flex: 1;
  margin-bottom: 0;
}

.task-input {
  width: 100%;
  padding: 0.8rem 1rem;
  border: 1px solid var(--light-gray);
  border-radius: var(--border-radius);
  font-size: 1rem;
  transition: border-color 0.3s;
}

.task-input:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 2px rgba(108, 99, 255, 0.2);
}

.priority-select,
.recurrence-select,
.due-date-input {
  width: 100%;
  padding: 0.8rem;
  border: 1px solid var(--light-gray);
  border-radius: var(--border-radius);
  font-size: 0.9rem;
  background-color: var(--white);
}

.add-btn {
  background-color: var(--primary-color);
  color: var(--white);
  border: none;
  padding: 0.8rem 1.5rem;
  border-radius: var(--border-radius);
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  transition: background-color 0.3s;
  width: 100%;
}

.add-btn:hover {
  background-color: var(--secondary-color);
}

/* Task Tabs */
.task-tabs {
  display: flex;
  margin-bottom: 1.5rem;
  border-bottom: 1px solid var(--light-gray);
}

.tab-btn {
  padding: 0.7rem 1.5rem;
  background: none;
  border: none;
  font-size: 0.95rem;
  font-weight: 500;
  color: var(--gray-color);
  cursor: pointer;
  position: relative;
  transition: color 0.3s;
}

.tab-btn.active {
  color: var(--primary-color);
}

.tab-btn.active::after {
  content: '';
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--primary-color);
}

/* Task Stats */
.task-stats {
  display: flex;
  justify-content: space-around;
  margin-bottom: 1.5rem;
  background-color: var(--white);
  padding: 1rem;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow);
}

.stat-item {
  text-align: center;
}

.stat-number {
  display: block;
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--primary-color);
}

.stat-label {
  font-size: 0.8rem;
  color: var(--gray-color);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Task List */
.task-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.empty-state {
  text-align: center;
  padding: 3rem;
  background-color: var(--white);
  border-radius: var(--border-radius);
  box-shadow: var(--shadow);
  color: var(--gray-color);
}

.empty-state i {
  font-size: 3rem;
  color: var(--primary-color);
  margin-bottom: 1rem;
  opacity: 0.7;
}

.empty-state p {
  font-size: 1.1rem;
}

/* Responsive Design */
@media (max-width: 768px) {
  

  .form-row {
    flex-direction: column;
    gap: 1rem;
  }

  .task-tabs {
    overflow-x: auto;
    padding-bottom: 0.5rem;
  }

  .task-stats {
    flex-direction: column;
    gap: 1rem;
  }
}

