/* src/Components/about.css */

/* Main container */
.about-container {
  max-width: 800px;
  margin: 2rem auto;
  padding: 2rem;
  background-color: #abe5f5;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
}

/* Title */
.about-container h1 {
  color: #2c3e50;
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
}

/* Paragraph text */
.about-container p {
  color: #34495e;
  font-size: 1.1rem;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

/* Back-to-home link */
.home-link {
  display: inline-block;
  margin-top: 1.5rem;
  padding: 0.5rem 1rem;
  background-color: #3498db;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  font-weight: 500;
  transition: background-color 0.3s;
}

.home-link:hover {
  background-color: #2980b9;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .about-container {
    padding: 1.5rem;
    margin: 1rem;
  }
  
  .about-container h1 {
    font-size: 2rem;
  }
}