.app-header {
  background-color: #93ddf9;
  color: rgb(3, 3, 3);
  padding: 1.5rem 0;
  text-align: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.header-content h1 {
  margin: 0;
  font-size: 2rem;
}

.header-content p {
  margin: 0.5rem 0 0;
  opacity: 0.9;
  font-size: 1rem;
