.task {
  border: 1px solid #e0e0e0;
  border-radius: 6px;
  padding: 12px;
  margin-bottom: 10px;
  background: white;
}

.task.completed {
  opacity: 0.7;
}

.task-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.task-main {
  display: flex;
  align-items: center;
  gap: 10px;
  flex-grow: 1;
}

.task-content {
  flex-grow: 1;
}

.task-content h3 {
  margin: 0 0 4px 0;
  font-size: 16px;
  font-weight: 500;
}

.task.completed h3 {
  text-decoration: line-through;
  color: #666;
}

.task-meta {
  display: flex;
  gap: 10px;
  font-size: 13px;
  color: #666;
}

.priority {
  text-transform: capitalize;
  padding: 2px 8px;
  border-radius: 10px;
  font-size: 12px;
}

.priority.high {
  background: #ffebee;
  color: #d32f2f;
}

.priority.medium {
  background: #fff8e1;
  color: #ffa000;
}

.priority.low {
  background: #e8f5e9;
  color: #388e3c;
}

.task-actions {
  display: flex;
  gap: 8px;
}

.task-actions button {
  padding: 6px 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 13px;
}

.subtask-toggle {
  background: #f5f5f5;
  color: #333;
}

.delete-task {
  background: #ffebee;
  color: #d32f2f;
}

.subtasks {
  margin-top: 12px;
  padding-top: 12px;
  border-top: 1px solid #eee;
}

.subtask-list {
  list-style: none;
  padding: 0;
  margin: 0 0 10px 0;
}

.subtask-list li {
  display: flex;
  align-items: center;
  padding: 6px 0;
  gap: 8px;
}

.subtask-list li.completed span {
  text-decoration: line-through;
  color: #999;
}

.no-subtasks {
  color: #999;
  font-size: 13px;
  margin: 8px 0;
  font-style: italic;
}

.add-subtask {
  display: flex;
  gap: 8px;
  margin-top: 8px;
}

.add-subtask input {
  flex-grow: 1;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.add-subtask button {
  padding: 8px 12px;
  background: #e3f2fd;
  color: #1976d2;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.add-subtask button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.delete-subtask {
  margin-left: auto;
  background: none;
  border: none;
  color: #999;
  cursor: pointer;
  padding: 0 4px;
}

.delete-subtask:hover {
  color: #d32f2f;
}